<template>
  <div class="request-details py-3">
    <div class="container">
      <!-- HEADING SECTION -->
      <b-col cols="12" class="step-heading">
        <h1
          class="second-step__title font-weight-semi font-size-large text-center" id="color-head">
          {{ $t('form.postingRequest.preview.heading') }}
        </h1>
        <h1
          class="second-step__title font-weight-semi font-size-large text-center" >
          {{ $t('form.postingRequest.preview.preview') }}
        </h1>
      </b-col>
      <!-- CARDS SECTION -->
      <b-col cols="12" md="9" class="mx-auto px-0">
        <div class="cards section" v-if="postingRequest.stayingDetails">
          <div class="card mx-2">
            <svg-icon
              fill="#262D3D"
              name="calendar"
              height="24"
              width="24"
              class="mb-2 ml-2"></svg-icon>
  
            <div class="price-tag">
              <span>
                {{ postingRequest.stayingDetails.selectedDuration }}
                {{ formating_price_type(postingRequest.stayingDetails.selectedDurationType) }}
              </span>
              <p>
                From,
                {{
                  $dateFns.format(
                    new Date(postingRequest.stayingDetails.startingDate),
                    'dd MMM yyyy'
                  )
                }}
              </p>
            </div>
          </div>
          <div class="card mx-2">
            <svg-icon
              fill="#262D3D"
              name="dollar"
              height="24"
              width="24"
              class="mb-2"></svg-icon>
  
            <div class="price-tag">
              <span>{{ postingRequest.stayingDetails.budgetRange }}</span>
              {{ $t('common.egp') }}
              <p>
                {{ formating_price_type_ly(postingRequest.stayingDetails.selectedDurationType) }}
              </p>
            </div>
          </div>
        </div>
      </b-col>
      <!-- TERMS SECTION -->
      <div class="section mx-auto col-md-9 col-12 py-0">
        <div class="container mb-3">
          <p class="heading font-size-semi-large font-weight-bold">
            {{ $t('form.postingRequest.preview.your_terms') }}
          </p>
          <span class="chip" v-for="(term, index) in checkboxesTerms" :key="index">
            {{ term.name }}
          </span>
        </div>
        <div class="container mt-3">
          <b-col cols="12" class="m-0 p-0" v-for="term in otherTerms" :key="term.id">
            <div :class="['details-container', 'col-12', 'staying_details', isNaN(term.value) ? 'no-border' : '']">
              <b-row>
                <h4 class="gray m-0">
                  {{ term.name }}
                </h4>
                <span>{{ term.value }}</span>
              </b-row>
            </div>
          </b-col>
        </div>
      </div>

      <!-- FURNISHING STATUS SECTION -->
      <div class="section mx-auto col-md-9 col-12" v-if="postingRequest.selectPreferences">
        <div class="container">
          <p class="heading font-size-semi-large font-weight-bold">
            {{ $t('form.postingRequest.preview.furnishing_status') }}
          </p>
          <span
            class="chip"
            v-for="(furnishType, index) in selectedFurnishTypes"
            :key="index">
            {{ furnishType.text }}
          </span>
        </div>
      </div>
      <!-- ROOMS SECTIONS -->
      <div class="section mx-auto col-md-9 col-12" v-if="postingRequest.selectPreferences">
        <div class="container">
          <div class="number-counter">
            <p>
              {{ $t('form.postingRequest.preview.minimum_rooms') }}
            </p>
            <div class="number">
              <span>
                {{ postingRequest.selectPreferences.miniumRooms || 1 }}
              </span>
            </div>
          </div>
        </div>
      </div>
      <!-- PROPERTY TYPES SECTION -->
      <div class="section mx-auto col-md-9 col-12">
        <div class="container">
          <p class="heading font-size-semi-large font-weight-bold">
            {{ $t('property.property_types') }}
          </p>
          <span class="chip" v-for="(type, index) in propertyType" :key="index">
            {{ type.prop_type_name }}
          </span>
        </div>
      </div>

      <div class="actions justify-content-center mx-auto col-md-9 col-12 mt-3">
        <!-- <div class="container"> -->
          <b-button @click="() => this.$router.push(this.localePath('/requests/post/terms'))">
            {{ $t('common.back') }}
          </b-button>
          <b-button variant="primary" @click="$emit('submitRequest', moveOutDate)">
            {{ $t('form.postingRequest.preview.submit') }}
          </b-button>
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'PostingPreview',
  props: {
    conditionsData: {
      type: Array,
      default: [],
    },
    propertiesData: Object,
  },
  data() {
    return {
      moveOutDate: null,
      postingRequest: {},
      checkboxesTerms: [],
      otherTerms: [],
      propertyCategory: {},
    };
  },
  methods:{
    formating_price_type(item){
      switch (item){
        case 'dy':
          return this.$t('form.postingRequest.stayingDetails.days')
        case 'my':
          return this.$t('form.postingRequest.stayingDetails.months')
        case 'yy':
          return this.$t('form.postingRequest.stayingDetails.years')
        case 'hr':
          return this.$t('form.postingRequest.stayingDetails.hours')
        case 'wy':
          return this.$t('form.postingRequest.stayingDetails.weeks')
        default:
          return false  
      }
    },
    formating_price_type_ly(item){
      switch (item){
        case 'dy':
          return this.$t('common.daily')

        case 'my':
          return this.$t('common.monthly')

        case 'yy':
          return this.$t('common.yearly')
          
        case 'hr':
          return this.$t('common.hourly')

        case 'wy':
          return this.$t('common.weekly')

        default:
          return false  
      }
    },
  },
  computed: {
    selectedFurnishTypes() {
      if (this.propertiesData.furnishType)
        return this.propertiesData.furnishType.filter((type) =>
          this.postingRequest.selectPreferences?.furnishType?.some(
            (term) => term === type.id
          )
        );

      return [];
    },
    propertyType() {
      return this.propertiesData?.propertyType?.filter((type) =>
        this.postingRequest?.selectPreferences?.propertyType?.some(
          (p) => p === type.prop_type_id
        )
      );

      return [];
    },
  },
  async mounted() {
    let conditionsPreview = localStorage.getItem('conditionsPreview') ? JSON.parse(localStorage.getItem('conditionsPreview')) : this.$router.push(this.localePath('/requests/post/property-category'));

    let checkboxesConditions = conditionsPreview.filter(condition => condition.name === "");
    this.otherTerms = conditionsPreview.filter(condition => condition.name !== "");

    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });

    this.postingRequest = localStorage.getItem('request') && JSON.parse(localStorage.getItem('request'));
    this.propertyCategory = this.postingRequest.propertyCategory;


    const conditionRes = await axios.get(`https://apibeta.rentup.co/api/tags/${this.propertyCategory.id}/conditionByTag`);

    for(let conditionId of checkboxesConditions){
      for(let condition of conditionRes.data.Data){
        if(conditionId.id === condition.id){
          this.checkboxesTerms.push(condition)
        }
      }
    }

  },
};
</script>

<style lang="scss" scoped>
#color-head{
  color: #1c7db6;
}
.details-container{
  background: #ffffff;
  padding: 0px 25px 20px;
  overflow: hidden;
  &.no-border {
    .row span {
      border: none !important;
    }
  }
  .prop-type{
    border: 0px !important;
  }
  .title-section{
    margin-bottom: 15px;
  }
  .row{
    gap: 20px;
    justify-content: space-between;
    span{
      border: 1px solid black;
      border-radius: 50%;
      padding: 5px 10px;
    }
  }
  .details-item {
    width: calc((100% / 2) - 10px);
    text-align: center;
    padding: 15px;
    border: solid 1px #E3E5E5;
    border-radius: 10px;
    h3{
      text-align: center;
      margin: 15px 0px 0px;
      color: black;
      font-weight: bold;
    }
    span{
      font-size: 12px;
      color: #48535B;
    }
  }
}
.request-details {
  min-height: 100vh;
  background-color: #f7f9fa;

  .section {
    background-color: white;
    padding: 32px 0;
    margin-top: 18px;
  }

  .chip {
    display: inline-block;
    padding: 8px 12px;
    background-color: #e6f2ff;
    color: #0078ff;
    font-weight: bold;
    border-radius: 32px;
    margin: 0px 12px 12px 0;

    &:last-child {
      margin: 0;
    }
  }

  .heading {
    font-weight: bold;

    .container {
      display: flex;
      justify-content: space-between;
    }

    p {
      font-size: 18px;
      margin: 0;
    }

    .btn-primary {
      background-color: #e6f2ff;
      color: #0078ff;
      border: none;
      font-weight: bold;
      border-radius: 12px;
    }
  }

  .cards {
    display: flex;
    justify-content: space-around;

    .card {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding: 1rem;
      height: 156px;
      width: 170px;
      border: 1px solid #e3e5e5;
      border-radius: 32px;

      .price-tag {
        text-align: center;
        font-weight: bold;
        font-size: 16px;
        p {
          font-weight: 300;
          color: #48535b;
          text-align: center;
          font-size: 15px;
        }
      }
    }
  }

  .address {
    .container {
      display: flex;
      align-items: flex-start;

      .svg-icon {
        margin: 2px 16px 0 0;
      }
    }
  }

  .number-counter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e3e5e5;

    &:last-child {
      border: none;
    }

    p {
      margin: 0;
      font-weight: 600;
      font-size: 16px;
    }

    .number {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      border: 1px solid #e3e5e5;
      border-radius: 50%;
    }
  }

  .actions {
    display: flex;
    padding: 24px 16px 16px;
    background-color: white;
    margin-top: 16px;
    gap: 10px;

    .btn {
      padding-top: 12px;
      padding-bottom: 12px;
      border-radius: 12px;
      font-weight: 600;
      border: none;
      max-width: 260px !important;
    }

    .btn-secondary {
      background-color: #f7f9fa;
      color: #111111;
      flex: 1;
      margin-right: 14px;
    }

    .btn-primary {
      background-color: #0078ff;
      flex: 3;
    }
  }
}

@media screen and (min-width: 768px) {
  .request-details {
    .cards {
      justify-content: center;

      .card {
        margin: 0 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .request-details {
    // padding-bottom: 96px;
    // padding-bottom: 20px;

    .actions {
      // position: fixed;
      // bottom: 0;
      // left: 0;
      width: 100%;
      margin: 0;
    }
  }
}
</style>
