<template>
  <div>
    <div class="px-3 back_close d-flex justify-content-between align-items-center py-3 d-md-none">
      <b-icon font-scale="1.5" icon="arrow-left" @click="$router.go(-1)"></b-icon>
      <b-icon icon="x-lg" @click="$router.push(localePath('/'))"></b-icon>
    </div>
    <b-progress class="w-100 mt-lg-2" :value="5" :max="5" :height="progressHeight"></b-progress>
    <div class="p-3 step">
      <!-- <ProgressCircle :current-step="5" :total-steps="5"></ProgressCircle> -->
      <PostingPreview
        :conditionsData="userConditions"
        :propertiesData="propertiesData"
        @submitRequest="handleSubmitClick" />
    </div>
    <WarningModal
      :show="showWarningModal"
      :routeTo="routeTo"
      @closeModal="showWarningModal = false"
      @closeModalAndNavigate="closeModalAndNavigate" />
  </div>
</template>

<script>
import PostingPreview from '@/components/postingRequestForm/PostingPreview';
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';
import WarningModal from '@/components/postingRequestForm/WarningModal';

import { createNamespacedHelpers } from 'vuex';
const { mapState } = createNamespacedHelpers('UserModule');
const { mapState: mapPropertyState } =
  createNamespacedHelpers('PropertyModule');
const { mapActions: mapAuthActions } = createNamespacedHelpers('AuthModule');
const { mapActions } = createNamespacedHelpers('PropertyModule');
export default {
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  components: {
    PostingPreview,
    ProgressCircle,
    WarningModal,
  },
  data() {
    return {
      propertiesData: {},
      userConditions: [],
      request: {},
      showWarningModal: false,
      routeTo: '',
      navigate: false,
    };
  },
  head() {
    return {
      title: `${
        this.dir === 'ltr' ? 'Posting Request | Step 4' : 'نشر طلب | الخطوة 4'
      } ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content:
            this.dir === 'ltr'
              ? 'Posting Request | Preview Request'
              : 'نشر طلب | نظرة عامة على الطلب ',
        },
      ],
    };
  },
  computed: {
    ...mapState(['user']),
    ...mapPropertyState(['postRequestFormSubmitted']),
    appEnv() {
      return this.$config.APP_ENV;
    },
    covertFurnishIdsToText() {
      if (this.propertiesData.furnishType)
        return this.propertiesData.furnishType
          .filter((type) =>
            this.request.selectPreferences?.furnishType?.some(
              (term) => term === type.id
            )
          )
          .map((t) => t.text);

      return [];
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    progressHeight(){
      if(process.client){
        if(window.innerWidth <= 991){
          return '3px'
        }else{
          return '5px'
        }
      }
    }
  },
  methods: {
    ...mapAuthActions(['showAuthModal']),
    ...mapActions(['postRequest']),
    async handleSubmitClick(moveOutDate) {
      const {
        propertyCategory,
        selectedDestination,
        stayingDetails,
        selectPreferences,
      } = this.request;

      const userConditions =
        localStorage.getItem('conditions') &&
        JSON.parse(localStorage.getItem('conditions'));

      let stayingDuration = this.$dateFns.differenceInDays(
        new Date(moveOutDate),
        new Date(stayingDetails.startingDate)
      );
      const { city, district, neighborhood } = selectedDestination;
      let locationId = neighborhood ? neighborhood : district ? district : city;
      const payload = {
        type: 1, // public request
        term: [stayingDetails.selectedDurationType.toUpperCase()],
        move_in_date: stayingDetails.startingDate,
        move_out_date: stayingDetails.move_out_date,
        budget: stayingDetails.budgetRange,
        num_of_bed_rooms: selectPreferences.miniumRooms,
        location_id: locationId,
        property_type_ids: selectPreferences.propertyType,
        conditions: userConditions,
        furnishing_status: this.covertFurnishIdsToText,
        // tag_id: propertyCategory.id,
        tag_id: 10,
      };

      console.log("#####################");
      console.log(payload);

      if(!this.user){
        this.showAuthModal({
          callbackAction: {},
        });
      }else{
        this.$axios
          .$post('/requests', payload)
          .then((response) => {
            if(response.Status === 200){
              localStorage.removeItem('request')
              localStorage.removeItem('conditions')
              localStorage.removeItem('conditionsPreview')
              this.$router.push(this.localePath('/dashboard/requests'));
              let body = this.$t('form.successRequest');
              this.$bvToast.toast(body, {
                title: this.$t('form.fillInput'),
                variant: 'primary',
                toaster: 'b-toaster-top-center',
                solid: true,
                autoHideDelay: 1000,
              });
              // location.reload();
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    transformUserTerms() {
      this.userConditions = this.userConditions.filter((condition) => {
        if (
          this.request.selectPreferences.userTerms.indexOf(condition.id) >= 0
        ) {
          return {
            condition_id: condition.id,
          };
        }
      });

      return this.userConditions.map((condition) => {
        return {
          condition_id: condition.id,
        };
      });
    },
    closeModalAndNavigate() {
      this.showWarningModal = false;
      this.navigate = true;
    },
  },
  async created() {
    const { Data } = await this.$axios.$get('/getPropMasterData');
    this.propertiesData = Data;
  },
  async mounted() {
    const currentRequest =
      localStorage.getItem('request') &&
      JSON.parse(localStorage.getItem('request'));

    if (!!currentRequest) this.request = currentRequest;
    else this.$router.push('/requests/post/property-category');

    const term = localStorage.getItem('request')
      ? JSON.parse(localStorage.getItem('request')).stayingDetails
          .selectedDurationType === 'days'
        ? 0
        : 1
      : 0;

    const { Data } = await this.$axios.$get(`/conditions?term=${term}`);
    this.userConditions = Data.conditions;
  },
  beforeRouteLeave(to, from, next) {
    if (this.postRequestFormSubmitted) return next();
    if (!to.path.includes('/requests/post/') && localStorage.getItem('request')) {
      this.showWarningModal = true;
      this.routeTo = to.path;

      if (this.navigate) return next();
    } else next();
  },
};
</script>

<style lang="scss">
.back_close {
  background-color: #f2f2f2 !important;
}
.step {
  margin-top: 20px;
}
</style>